  background: #e5c3b2;
  height:auto;
  border-radius:0 50px 50px 0;
}
.semicircle-right:before {
  content:'';
  display:inline-block;
  width:100%;
  padding-top:100%;
}
.semicircle-right:after {
  content:'';
  display:inline-block;
  width:100%;
  padding-top:100%;
}
